.black-box{
  border:solid 2px black;
  color: black;
}


.white-box{
  background: white;
  color: black;
}


$spmenu-width:300px;
.spMenuBox{
  width:$spmenu-width;
  background: green;
}

.spMenuBox__header{
  background: green;
}

.button{
  background: white;
  &.--cart{
    display: inline-block;
    @extend .black-box;
  }

  &.--signup{
    display: inline-block;
    @extend .black-box;
  }
}

$menu-font-size:15px;
.menu__item{
  @extend .white-box;
  line-height: 1;
  font-size: $menu-font-size;
}

$menu-content-margin:3px;
$menu-item-padding:3px;
.spMenuBox__contentBox{
  @extend .white-box;
  margin-bottom: $menu-content-margin;
  padding:$menu-item-padding;
}

$menu-title-font-size:30px;
.menu__title{
  font-size: $menu-title-font-size;
}

.gridLayout__item{
  width: calc($spmenu-width / 2);
  @extend .black-box;
  display: inline-block;
}
